.action-link {
  flex-shrink: 0;
  padding-top: 17px;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 15px;
  border-radius: 50px;

  font-family: $roboto-regular;
  font-size: $nav-fs-d;
  text-align: center;
  color: $action-link-color-static;

  background-color: $action-link-bg-color;


  &:hover,
  &:focus {
    color: $action-link-bg-color;

    background-color: $action-link-color-static;

    outline: none;

    transition-duration: 0.5s;
    transition-property: background-color;
  }

  &--shadow {
    @media (max-width: $tablet-max-width) {
      color: $action-link-bg-color;

      background-color: $nav-font-color-static;
    }

    @media (min-width: $desktop-min-width) {
      transition-property: box-shadow, background-color;
      transition-duration: 2s, 0.5s;

      box-shadow: 0 0 45px 0 rgba(1, 125, 249, 0.2);

      &:hover,
      &:focus {
        box-shadow: none;
      }
    }
  }

  &--nav-closed {
    @media (max-width: $mobile-max-width) {
      display: none;
    }

    @media (max-width: $tablet-max-width) {
      color: $action-link-color-static;

      background-color: $action-link-bg-color;
      box-shadow: 0 0 25px 0 rgba(1, 125, 249, 0.2);

      &:hover,
      &:focus {
        box-shadow: none;

        transition-property: box-shadow, background-color;
        transition-duration: 1s, 0.5s;
      }
    }
  }
}
