html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  background-color: $bg-body;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.global-wrapper {
  min-width: $mobile-min-width;
  max-width: $desktop-max-width;
  margin-left: auto;
  margin-right: auto;
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}

.site-list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;

  list-style: none;
}
