.main-nav {
  display: flex;

  @media (max-width: $tablet-max-width) {
    position: relative;
    top: 0;

    flex-direction: column;
    align-items: center;
    width: 100%;
    padding-top: 35px;
    border-top: $nav-border;

    background-color: $nav-bg-color-mt;

    transition-property: top, position;
    transition-duration: 2s;

    &--closed {
      position: absolute;
      top: -430px;
      z-index: 0;

      display: none;
      background-color: $bg-body;
    }
  }

  @media (min-width: $desktop-min-width) {
    align-items: flex-start;
  }
}

.main-nav__list {
  font-family: $roboto-regular;
  line-height: $global-lh;

  @media (max-width: $mobile-max-width) {
    font-size: $nav-fs-m;
  }

  @media (min-width: $tablet-min-width) and (max-width: $tablet-max-width) {
    font-size: $nav-fs-t;
  }

  @media (max-width: $tablet-max-width) {
    margin-bottom: 20px;
  }

  @media (min-width: $desktop-min-width) {
    display: flex;
    flex-wrap: wrap;
    margin-top: 15px;
    margin-right: 45px;

    font-size: $nav-fs-d;
  }
}

.main-nav__item {
  @media (max-width: $tablet-max-width) {
    display: flex;
    justify-content: center;

    &:not(:last-of-type) {
      margin-bottom: 20px;
    }
  }

  &:not(:last-child) {
    @media (min-width: $desktop-min-width) {
      margin-right: 30px;
      margin-bottom: 20px;
    }
  }
}

.main-nav__link {
  color: $nav-font-color-static;

  &:hover,
  &:focus {
    color: $nav-font-color-hover;
    outline: none;
    transition-duration: 0.3s;

    @media (max-width: $tablet-max-width) {
      &::before,
      &::after {
        background-color: $nav-font-color-hover;
      }
    }
  }

  &:active {
    color: $nav-font-color-active;

    @media (max-width: $tablet-max-width) {
      &::before,
      &::after {
        background-color: $nav-font-color-active;
      }
    }
  }

  @media (max-width: $tablet-max-width) {
    display: flex;
    align-items: center;
    justify-content: center;

    text-align: center;

    &::before,
    &::after {
      content: "";

      width: 60px;
      height: 1px;

      background-color: $nav-font-color-static;
    }

    &::before {
      margin-right: 15px;
    }

    &::after {
      margin-left: 15px;
    }
  }

  @media (min-width: $desktop-min-width) {
    &:hover,
    &:focus {
      padding-bottom: 5px;

      box-shadow: 0 3px 0 -1px $nav-font-color-hover;
    }

    &:active {
      box-shadow: 0 3px 0 -1px $nav-font-color-active;
    }
  }
}
