.page-header {
  position: relative;
  z-index: 1;

  display: flex;

  @media (max-width: $mobile-max-width) {
    padding-top: 15px;
    padding-bottom: 15px;

    &--nav-closed {
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  @media (min-width: $tablet-min-width) and (max-width: $tablet-max-width) {
    padding-top: 30px;
    padding-bottom: 50px;

    &--nav-closed {
      width: 90%;
    }
  }

  @media (max-width: $tablet-max-width) {
    flex-direction: column;
    align-items: center;
    margin-left: auto;
    margin-right: auto;

    background-color: $nav-bg-color-mt;
    border-bottom: $nav-border;

    &--nav-closed {
      flex-direction: row;
      align-items: flex-start;

      background-color: transparent;
      border-bottom: none;
    }
  }

  @media (min-width: $desktop-min-width) {
    flex-wrap: wrap;
    align-items: flex-start;
    width: $narrow-content-d;
    margin-left: auto;
    margin-right: auto;
    padding-top: 47px;
  }
}

.page-header__nav {
  @media (min-width: $desktop-min-width) {
    margin-left: auto;
  }
}

.page-header__logo {
  flex-shrink: 0;

  &:hover,
  &:focus {
    outline: none;
    filter: drop-shadow(0 0 10px $header-logo-shadow);
  }

  @media (max-width: $mobile-max-width) {
    margin-bottom: 15px;

    &--nav-closed {
      margin-bottom: 0;
    }
  }

  @media (min-width: $tablet-min-width) and (max-width: $tablet-max-width) {
    margin-bottom: 25px;

    &--nav-closed {
      margin-top: 10px;
      margin-bottom: 0;
    }
  }

  @media (max-width: $tablet-max-width) {
    position: relative;
    z-index: 1;

    display: flex;
    justify-content: center;

    &--nav-closed {
      align-self: flex-start;
    }
  }

  @media (min-width: $desktop-min-width) {
    width: 86px;
    margin-top: 10px;
    margin-right: 60px;
    margin-bottom: 30px;
  }
}

.page-header__link {
  max-width: 300px;

  @media (max-width: $mobile-max-width) {
    margin-bottom: 10px;
  }
}

.page-header__nav-toggle {
  @media (max-width: $mobile-max-width) {
    &--nav-closed {
      margin-top: 5px;
    }
  }

  @media (min-width: $tablet-min-width) and (max-width: $tablet-max-width) {
    &--nav-closed {
      margin-top: 15px;
      margin-right: 30px;
    }
  }

  @media (max-width: $mobile-max-width) {
    &--nav-opened {
      top: 70px;
      right: 10px;
    }
  }

  @media (min-width: $tablet-min-width) and (max-width: $tablet-max-width) {
    &--nav-opened {
      top: 100px;
      right: 20px;
    }
  }

  @media (max-width: $tablet-max-width) {
    padding: 0;
    border: none;
    background: none;

    cursor: pointer;

    &:hover,
    &:focus {
      &::before,
      &::after {
        background-color: $nav-font-color-hover;
      }
    }

    &:active {
      &::before,
      &::after {
        background-color: $nav-font-color-active;
      }
    }

    &--nav-closed {
      width: 30px;
      height: 3px;
      margin-left: auto;

      background-color: $nav-font-color-static;

      &:hover,
      &:focus {
        outline: none;
        background-color: $nav-font-color-hover;
      }

      &:active {
        background-color: $nav-font-color-active;
      }

      &::before,
      &::after {
        content: "";

        display: block;
        width: 30px;
        height: 3px;

        background-color: $nav-font-color-static;
      }

      &::before {
        margin-top: 9px;
      }

      &::after {
        margin-top: 6px;
      }
    }

    &--nav-opened {
      position: absolute;
      z-index: 2;

      width: 25px;
      height: 25px;

      transform: rotate(45deg);
      outline: none;

      &::before,
      &::after {
        content: "";

        position: absolute;

        display: block;

        background-color: $nav-font-color-static;
      }

      &::before {
        bottom: 0;
        left: 11px;

        width: 3px;
        height: 25px;
      }

      &::after {
        right: 0;
        top: 11px;

        width: 25px;
        height: 3px;
      }
    }
  }

  @media (min-width: $desktop-min-width) {
    display: none;
  }
}
