.search {
  background-color: $search-bg-color;

  @media (max-width: $mobile-max-width) {
    padding-top: 40px;
    padding-bottom: 30px;
  }

  @media (min-width: $tablet-min-width) {
    padding-top: 55px;
    padding-bottom: 30px;
  }
}

.search-form {
  display: flex;
  margin-left: auto;
  margin-right: auto;

  @media (max-width: $mobile-max-width) {
    flex-direction: column;
    width: $narrow-content-m;
  }

  @media (min-width: $tablet-min-width) {
    align-items: center;
  }

  @media (min-width: $tablet-min-width) and (max-width: $tablet-max-width) {
    width: $narrow-content-t;
  }

  @media (min-width: $desktop-min-width) {
    width: $narrow-content-d;
  }
}

.search-form__wrapper {
  display: flex;

  @media (max-width: $mobile-max-width) {
    flex-direction: column;
  }

  @media (min-width: $tablet-min-width) {
    flex-wrap: wrap;
    width: 96%;
  }

  @media (min-width: $tablet-min-width) and (max-width: $tablet-max-width) {
    margin-left: -8%;
  }

  @media (min-width: $desktop-min-width) {
    margin-left: -3%;
  }
}

.search-form__label {
  position: relative;

  border-bottom: 1px solid $search-static-fc;

  @media (max-width: $mobile-max-width) {
    margin-bottom: 30px;
  }

  @media (min-width: $tablet-min-width) and (max-width: $tablet-max-width) {
    margin-left: 8%;
    margin-bottom: 45px;
  }

  @media (min-width: $desktop-min-width) {
    min-width: 10%;
    margin-left: 3%;
    margin-bottom: 30px;
  }

  &:hover,
  &:focus {
    color: $search-hover-fc;
    border-bottom-color: $search-hover-fc;

    cursor: pointer;

    &::before {
      opacity: 1;
    }
  }

  &::before {
    content: "";

    position: absolute;
    top: 0;
    left: 0;

    display: block;
    width: 22px;
    height: 22px;

    background-repeat: no-repeat;
    background-position: left top;
    background-size: 22px 22px;

    opacity: 0.7;
  }

  &--city {
    @media (min-width: $tablet-min-width) {
      min-width: 39%;
    }

    @media (min-width: $desktop-min-width) {
      min-width: 17%;
    }

    &::before {
      background-image: url("../img/location.svg");
    }
  }

  &--checkin,
  &--checkout {
    &::before {
      background-image: url("../img/calendar.svg");
    }
  }

  &--checkin {
    @media (min-width: $tablet-min-width) and (max-width: $tablet-max-width) {
      min-width: 18%;
    }

    @media (min-width: $desktop-min-width) {
      min-width: 11%;
    }
  }

  &--checkout {
    @media (min-width: $tablet-min-width) and (max-width: $tablet-max-width) {
      min-width: 19%;
    }

    @media (min-width: $desktop-min-width) {
      min-width: 12%;
    }
  }

  &--rooms {
    &::before {
      background-image: url("../img/house.svg");
    }
  }

  &--adults {
    &::before {
      background-image: url("../img/man.svg");
    }
  }

  &--rooms,
  &--adults {
    @media (min-width: $desktop-min-width) {
      min-width: 9%;
    }
  }

  &--children {
    &::before {
      top: 1px;

      background-image: url("../img/child.svg");
    }
  }

  &--stars {
    @media (min-width: $desktop-min-width) {
      min-width: 8%;
    }

    &::before {
      top: -1px;

      background-image: url("../img/star.svg");
    }
  }

  &--rooms,
  &--adults,
  &--children,
  &--stars {
    @media (min-width: $tablet-min-width) and (max-width: $tablet-max-width) {
      min-width: 17%;
    }
  }
}

.search-form__select {
  position: relative;
  z-index: 1;

  display: block;
  min-width: 100%;
  padding-left: 35px;
  padding-bottom: 10px;
  border: none;

  font-family: $roboto-regular;
  font-size: $search-fs;
  line-height: $global-lh;
  color: $search-static-fc;

  background-color: transparent;

  appearance: none;

  &:hover,
  &:focus {
    color: $search-hover-fc;

    outline: none;
    cursor: pointer;
  }
}

.search-form__option {
  color: $search-option-fc;
}

.search-form__option--title {
  background-color: $search-option-title-background;
}

.search-form__submit {
  border: none;

  background-color: $search-btn-back-static;
  background-image: url("../img/search.svg");
  background-position: center;
  background-size: 18px 18px;
  background-repeat: no-repeat;

  cursor: pointer;

  @media (max-width: $mobile-max-width) {
    max-width: 380px;
    width: 50%;
    min-width: 50px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
    padding-right: 50px;
    border-radius: 50px;

    background-position: right 20px center;
  }

  @media (min-width: $tablet-min-width) {
    margin-left: auto;
    padding: 0;
    border-radius: 50%;
  }

  @media (min-width: $tablet-min-width) and (max-width: $tablet-max-width) {
    width: 45px;
    height: 45px;
    margin-top: -30px;
  }

  @media (min-width: $desktop-min-width) {
    width: 40px;
    height: 40px;
    margin-top: -30px;
  }

  &:hover,
  &:focus {
    background-color: $search-btn-back-hover;
    outline: none;
  }

  &:active {
    box-shadow: 0 0 0 2px $search-btn-active-bc;
  }
}

.search-form__submit-text {
  @media (max-width: $mobile-max-width) {
    font-family: $roboto-regular;
    line-height: $global-lh;
    font-size: $search-fs;
    color: $search-btn-fc;
  }

  @media (min-width: $tablet-min-width) {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;

    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0, 0, 0, 0);
    overflow: hidden;
  }
}
