.call-to-action {
  position: relative;
  z-index: 0;

  background-color: #d2dbe4;
  background-repeat: no-repeat;
  background-position: center top;

  @media (max-width: $mobile-max-width) {
    min-height: 353px;
    margin-top: -60px;
    padding-top: 105px;
    padding-bottom: 25px;

    background-image: url("../img/call-to-action-bg-mobile.jpg");
  }

  @media (min-width: $tablet-min-width) and (max-width: $tablet-max-width) {
    min-height: 500px;
    margin-top: -130px;
    padding-top: 165px;
    padding-bottom: 45px;

    background-image: url("../img/call-to-action-bg-tablet.jpg");
  }

  @media (min-width: $desktop-min-width) {
    padding-bottom: 80px;
  }


  @media (min-width: $desktop-min-width) {
    min-height: 893px;
    margin-top: -120px;
    padding-top: 236px;

    background-image: url("../img/call-to-action-bg-desktop.jpg");
  }
}

.call-to-action__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.call-to-action__country-name {
  max-width: 65%;

  font-family: $molle;
  line-height: $global-lh;
  color: $call-to-action-fc;
  text-align: center;
  font-weight: normal;

  @media (max-width: $mobile-max-width) {
    font-size: $call-to-action-county-fs-m;
  }

  @media (min-width: $tablet-min-width) {
    font-size: $call-to-action-county-fs-td;
  }
}

.call-to-action__text {
  max-width: 65%;

  font-size: $call-to-action-fs;
  font-family: $roboto-regular;
  line-height: $global-lh;
  color: $call-to-action-fc;
  text-align: center;
  text-transform: uppercase;

  @media (max-width: $mobile-max-width) {
    margin-bottom: 20px;

    letter-spacing: 2px;
  }

  @media (min-width: $tablet-min-width) and (max-width: $tablet-max-width) {
    letter-spacing: 5px;
  }

  @media (min-width: $tablet-min-width) {
    margin-bottom: 20px;

    &:first-of-type {
      margin-top: -35px;
    }
  }
}

.call-to-action__link {
  padding-left: 53px;
  padding-right: 53px;

  @media (max-width: $mobile-max-width) {
    max-width: 65%;
    margin-top: 10px;
    margin-bottom: 10px;
  }

  @media (min-width: $tablet-min-width) {
    max-width: 400px;
    margin-top: 26px;
    margin-bottom: 45px;
  }
}
